import React from 'react';

import './App.css';
import 'graphiql/graphiql.min.css';
import GraphiQL from 'graphiql';

import Grid from '@material-ui/core/Grid';
import { MuiThemeProvider } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';

export default class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = { authToken: '' };
        this.updateAuthToken = this.updateAuthToken.bind(this);
        this.graphQLFetcher = this.graphQLFetcher.bind(this);
    }

    getAuthToken() {
        return this.state.authToken;
    }

    updateAuthToken(event) {
        this.setState({ authToken: event.target.value });
    }

    graphQLFetcher(graphQLParams) {
        const URL = 'https://analytics-api.choreo.dev/query-api';
        const token = this.getAuthToken();
        return fetch(URL, {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + token
            },
            body: JSON.stringify(graphQLParams),
            credentials: 'same-origin'
        }).then((response) => response.json());
    }

    render() {
        const defaultQuery = `
        query(
            $dataFilter: DataFilter!,
            $filter: TimeFilter!
            ) {
                getTotalTraffic(filter: $filter, dataFilter: $dataFilter)
                getOverallLatency(filter: $filter, dataFilter: $dataFilter)
                getTotalErrors(filter: $filter, dataFilter: $dataFilter)
            }
        `;

        const defaultVariables = JSON.stringify({
            filter: {
                from: '2021-08-23T10:00:00.000+05:30',
                to: '2021-08-24T10:10:00.000+05:30'
            },
            latencySummaryFilter: {
                granularity: '1d'
            },
            granularity: '1d',
            dataFilter: {
                orgId: '12345678-1234-4321-1234-123456789012',
                environmentId: 'user-1',
                tenant: 'carbon.super'
            }
        });

        const { authToken } = this.state;

        return (
            <MuiThemeProvider>
                <Grid container spacing={1}>
                    <Grid item xs={12} className='Token'>
                        <Grid item xs={4} className='TokenLabel'>
                            <Typography variant='h6' gutterBottom>
                                Authentication Token:
                            </Typography>
                        </Grid>
                        <Grid item xs={7}>
                            <TextField
                                fullWidth
                                size='small'
                                variant='outlined'
                                placeholder='Please paste the authentication token here'
                                onChange={this.updateAuthToken}
                                value={authToken}
                            />
                        </Grid>
                    </Grid>
                </Grid>
                <GraphiQL fetcher={this.graphQLFetcher} defaultQuery={defaultQuery} variables={defaultVariables} />
            </MuiThemeProvider>
        );
    }
}
